.navMainContainer{
    display: flex;
    background-color: #eee;
    .navSubContainer{
        padding: 30px;
        width: 100%;
        padding-bottom: 0;
        margin-bottom: 0;
        display: flex;
        margin: 5px;
        justify-content: space-between;
        .navLink{
            color: black;
            display: flex;
            justify-content: space-around;
            text-decoration: none;
            font-size: 20px;
            .navTittle{
                color: blue;
                margin-left: 5px;
            }
        }
        .MainTittle{
            font-size: 40px;
            font-weight: bold;
        }
        .navButtonsContainer{
            width: 20vw;
            .navButtons{
                display: flex;
                justify-content: space-evenly;
                text-decoration: none;
                
            }
        }
    }
}


@media (max-width: 576px){
   .navMainContainer{
    .navSubContainer{
        flex-direction: column;
        align-items: center;
        .MainTittle{
            font-size: 20px;
        }
    }
    .navButtonsContainer{
        margin: 10px;
        display: flex;
        justify-content: center;
        .navLink{
            margin: 0 10px;
        }
    }
   }
}